// #BUTTONS

[type="button"],
[type="submit"] {
  position: relative;
  margin:  0;
  padding: 0;
  border:  0;
  outline: none; }

.btn {
  display: block;
  float: left;
  margin: 0.5rem 0;
  margin-right: 1rem;
  font-family: 'Cabin', sans-serif;
  font-weight: bold;
  letter-spacing: 0.02em;
  text-decoration: none;
  @include orange-gradient;
  color: $white;
  cursor: pointer;
  transition: all 0.15s ease-in-out;
  &:hover {
    background-color: $orange-light; }
  &:active {
    background-color: $orange-dark; }
  &:last-of-type {
    margin-right: 0; } }

.btn--block {
  width: 100%;
  flex: 0 1 100%; }

.btn.btn--sm {
  min-width: 7.5rem;
  min-height: 2.25rem;
  padding: 0 1.875rem;
  font-size: 14px; }

.btn.btn--md {
  min-width: 8.75rem;
  min-height: 3rem;
  padding: 0 1.125rem;
  font-size: 16px; }

.btn.btn--lg {
  min-width: 11.88rem;
  min-height: 3.75rem;
  padding: 0 1.5rem;
  font-size: 18px; }

.rc {
  border-radius: 0.375rem; }

.rnd {
  border-radius: 999rem; }

.btn--rec-md {
  width: 3rem;
  height: 3rem; }

.btn--rec-sm {
  width: 2.25rem;
  height: 2.25rem; }
